import React from 'react';
import styles from './Tooltip.module.css';

const Tooltip = ({ content, display, element }) => {
  const width = 100;
  const height = 42;
  const orientation = element ? element.y < 67 ? "left" : "top" : "top";
  const x = element ? orientation === "left" ? element.x - 100/2 + 16 : 0 : 0;
  const y = element ? orientation === "left" ? element.y : element.y - height - 16 : 0;
  const tooltipClass = orientation === "left" ? styles.tooltipLeft : styles.tooltip;

  if (display) {
    return (
      <div className={styles.container}>
        <div className={tooltipClass} style={
          {
            left: x,
            top: y,
            width: width,
            height: height,
          }
        }>
          <span className={styles.text}>{content}</span>
        </div>
        <div className={styles.arrow} />
      </div>
    );
  }
  return null;
};

export default Tooltip;
